import axios from 'axios'
import {jsonHeader} from '@/helpers/axiosHeaders'
import qs from "qs";
import Vue from 'vue'
import VueCookies from 'vue-cookies'

const state = {
    tests: ['1','2'],
    currentTest: '',
}
const getters = {
    getTest() {
        return state.tests[Math.floor((Math.random()*state.tests.length))];
    },
}
const actions = {
    run(context){
        let currentTest;
        let currentTestCookies = VueCookies.get('curABTest_detail');
        if(currentTestCookies !== null){
            currentTest = currentTestCookies;
            context.commit('currentTest',currentTest);
        }
        else{
            let currentTest = context.state.tests[Math.floor((Math.random()*context.state.tests.length))];
            context.commit('currentTest',currentTest);
            context.commit('setCookies',currentTest);
        }
    }
}
const mutations = {
    currentTest(state, data) {
        state.currentTest = data;
        
    },
    setCookies(state, data) {
        VueCookies.set('curABTest_detail',data, 60*60*24*30);
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}